@mixin mobile {
    @media screen and (max-width:480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width:768px) {
        @content;
    }
}

@mixin laptop {
    @media screen and (max-width:1200px) {
        @content;
    }
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

body {
    background: repeating-linear-gradient(-45deg, #ddd 0, #ddd 2px, #FFF 4px, #FFF 6px);
}

.app {
    height: 540px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(68, 68, 68);

    .calendar {
        height: 500px;
        width: 450px;

        .title {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 13px;
        }

        .yearAndMonth {
            padding: 2% 0;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
        }

        .btns {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 0 20px;

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 2px 7px;
                font-size: 16px;
                background-color: transparent;
                border: 1px solid black;
                border-radius: 5px;
                cursor: pointer;

                &:hover {
                    transform: scale(1.2);
                    opacity: 0.4;
                }
            }
        }

        .cal {
            margin-top: 8px;
            width: 100%;
            height: 300px;

            table {
                text-align: center;
                height: 100%;
                width: 100%;

                .sun {
                    color: red;
                }

                .sat {
                    color: blue;
                }

                .notCurrentMonth {
                    border-radius: 5px;
                    background-color: rgba(154, 153, 153, 0.8);
                }

                .today {
                    background-color: rgba(245, 245, 125, 0.8);
                    width: 100%;
                    height: 100%;
                    border-radius: 5px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}